/* src/pages/ProductDesign.css */
.product-design {
  padding: 100px 0;
  background: linear-gradient(135deg, var(--light-color) 0%, var(--accent-green) 100%);
}

.product-design .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: var(--dark-color);
}

.design-projects-grid {
  display: flex;
  justify-content: center;
  gap: 80px;
  margin-top: 50px;
}

.design-project {
  text-align: center;
  max-width: 400px;
}

.design-project-image {
  width: 35%;
  height: auto;
  margin-bottom: 20px;
}

.design-project:nth-child(2) .design-project-image {
  width: 38%;
}

.app-name {
  font-size: 1rem;
  line-height: 1.5;
  color: var(--text-color);
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .design-projects-grid {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
  
  .design-project-image {
    width: 40%;
  }
}