/* src/pages/DataIntelligence.css */
.data-intelligence {
  padding: 100px 0;
  background: linear-gradient(135deg, var(--soft-green) 0%, var(--mint-green) 100%);
}

.data-intelligence .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: var(--dark-color);
}

.project-section {
  margin-bottom: 60px;
}

/* Carousel Container */
.carousel-container {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
}

.projects-grid-wrapper {
  overflow: hidden;
  width: 100%;
  flex: 1;
}

.carousel-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(38, 102, 127, 0.2);
}

.carousel-btn:hover {
  background: var(--secondary-color);
  transform: scale(1.1);
}

.carousel-btn:active {
  transform: scale(0.95);
}

.projects-grid {
  display: flex;
  gap: 20px;
  padding: 20px 0;
  transition: transform 0.3s ease;
  width: calc(7 * 420px + 6 * 20px);
}

.projects-grid::-webkit-scrollbar {
  height: 8px;
}

.projects-grid::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.projects-grid::-webkit-scrollbar-thumb {
  background: var(--text-color);
  border-radius: 4px;
}

.projects-grid::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}

/* Carousel Dots */
.carousel-dots {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 8px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: #ccc;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background: var(--primary-color);
  transform: scale(1.2);
}

.dot:hover {
  background: var(--secondary-color);
}

.project-card {
  background: white;
  border-radius: 12px;
  padding: 15px;
  box-shadow: 0 4px 15px rgba(38, 102, 127, 0.1);
  transition: all 0.3s ease;
  border: 1px solid #e8f5e8;
  flex: 0 0 420px;
  height: fit-content;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(38, 102, 127, 0.2);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  height: 45px;
}

.project-image {
  height: 160px;
  background-size: 80% auto;
  background-repeat: no-repeat;
  background-position: center center;
  border-radius: 8px;
  margin-bottom: 25px;
  position: relative;
  background-color: #f8f9fa;
}

.project-date {
  background: var(--text-color);
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.company-logo-sticker {
  width: 90px;
  height: 45px;
  background: white;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.12);
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.company-logo-sticker img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 4px;
}

.project-title {
  font-size: 1.1rem;
  margin-bottom: 6px;
  color: var(--dark-color);
  line-height: 1.3;
}

.project-description {
  color: var(--text-color);
  margin-bottom: 8px;
  line-height: 1.5;
  font-size: 0.9rem;
}

.project-highlights {
  margin-bottom: 8px;
}

.highlight-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 3px;
  font-size: 0.8rem;
  line-height: 1.4;
}

.highlight-bullet {
  color: var(--primary-color);
  margin-right: 8px;
  margin-top: 2px;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 12px;
}

.tag {
  background: var(--accent-green);
  color: var(--text-color);
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 0.7rem;
  font-weight: 500;
}

.project-actions {
  text-align: center;
}

.view-details-btn {
  background: var(--primary-color);
  color: white;
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.85rem;
  transition: all 0.3s ease;
  display: inline-block;
}

.view-details-btn:hover {
  background: var(--secondary-color);
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .carousel-container {
    gap: 10px;
  }
  
  .carousel-btn {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
  
  .project-card {
    flex: 0 0 375px;
    padding: 12px;
  }
  
  .project-image {
    height: 107px;
  }
  
  .project-title {
    font-size: 1rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
}