/* src/pages/Home.css */
.portfolio-container {
  width: 100%;
}

.home {
  padding: 50px 0;
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background: linear-gradient(135deg, var(--light-color) 0%, var(--mint-green) 50%, var(--accent-green) 100%);
}

.home-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Profile Section */
.profile-section {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-bottom: 50px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  border: 4px solid #26667F;
  box-shadow: 0 8px 25px rgba(45, 106, 79, 0.3);
  transition: all 0.3s ease;
  animation: profileFloat 4s ease-in-out infinite;
  aspect-ratio: 1;
}

.profile-image:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 35px rgba(45, 106, 79, 0.4);
}

.text-content {
  flex: 1;
  text-align: left;
}

/* Typography */
.home-content h1 {
  font-size: 2.8rem;
  margin-bottom: 10px;
  color: var(--dark-color);
  font-weight: 700;
  white-space: nowrap;
}

.subtitle {
  font-size: 1.5rem;
  color: var(--blue-green);
  margin-bottom: 30px;
  font-weight: 600;
}

.hero-description {
  font-size: 1.3rem;
  color: var(--text-color);
  margin-bottom: 15px;
  line-height: 1.6;
}

.hero-tagline {
  font-size: 1.1rem;
  color: var(--text-color);
  font-style: italic;
  margin-bottom: 50px;
  opacity: 0.9;
}

/* Expertise Section */
.expertise-section {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.expertise-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.expertise-icon {
  font-size: 1.8rem;
}

.expertise-label {
  font-size: 0.85rem;
  font-weight: 500;
  color: var(--text-color);
}

.expertise-separator {
  font-size: 1.2rem;
  color: var(--text-color);
  font-weight: 300;
  opacity: 0.6;
}

/* Scroll Indicator */
.scroll-indicator {
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
}

.scroll-text {
  font-size: 0.9rem;
  color: var(--text-color);
  opacity: 0.7;
  text-align: center;
}

/* Scroll Arrow Styles */
.scroll-arrow-container {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 30px;
}

.scroll-arrow {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  animation: bounce 2s infinite;
}

.scroll-arrow span {
  display: block;
  width: 22px;
  height: 22px;
  border-bottom: 4px solid #26667F;
  border-right: 4px solid #26667F;
  transform: rotate(45deg);
  margin: -10px;
  animation: animate 2s infinite;
}

.scroll-arrow span:nth-child(2) {
  animation-delay: -0.2s;
}

.scroll-arrow span:nth-child(3) {
  animation-delay: -0.4s;
}

/* Animations */
@keyframes profileFloat {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes arrowMove {
  0%, 100% {
    opacity: 0.3;
    transform: translateX(-50%) translateY(0);
  }
  50% {
    opacity: 1;
    transform: translateX(-50%) translateY(5px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .profile-section {
    flex-direction: column;
    text-align: center;
    gap: 30px;
  }
  
  .text-content {
    text-align: center;
  }
  
  .profile-image {
    width: 150px;
    height: 150px;
  }
  
  .home-content h1 {
    font-size: 2.2rem;
  }
  
  .subtitle {
    font-size: 1.2rem;
  }
  
  .hero-description {
    font-size: 1.1rem;
  }
  
  .scroll-indicator {
    bottom: 20px;
  }
  
  .expertise-section {
    gap: 15px;
    margin-bottom: 30px;
  }
  
  .expertise-icon {
    font-size: 1.4rem;
  }
  
  .expertise-label {
    font-size: 0.75rem;
  }
  
  .expertise-separator {
    font-size: 1rem;
  }
}